<template>
	<section>
		<!-- transition-group : 목록에 애니메이션 달 때 사용.
		     1) tag="적용할 HTML 태그". 
			 2) 대상태그(여기선 li)에 :key="유일한 값" : 프레임워크에서 순서 제어하도록해서 브라우저가 돔 조작(렌더링)하는데 시간 줄이기. 
			 3) name="CSS 적용할 ID"-->
		<transition-group name="list" tag="ul">
			<li v-for="(item, index) in propsdata" :key="item" class="shaow">
				<i class="checkBtn fa fa-check" aria-hidden="true"></i>	
				{{ item }}
				<!-- item: localStroage 데이터 제거, index: data 값 제거 -->
				<span class="removeBtn" type="button" @click="removeItem(item, index)">
					<i class="fa fa-trash-o" aria-hidden="true"></i>
				</span>
			</li>
		</transition-group>
	</section>
</template>

<script>
export default {
	props: ['propsdata'],
	
	//created()
	//1. 뷰 인스턴스 생성되자마자 뷰 데이터에 접근할 수 있도록 created()에서 로컬 스토리지의 데이터를 뷰 데이터로 옮김.
	//2. this 사용 가능.

	//localStorage : 도메인마다 부여되는 저장소. 명시적 제거 외에 데이터를 그대로 갖고 있음. key, value 형태의 자료구조.
	
	methods: {
		removeItem(item, index) {
			this.$emit("removeOne", item, index);
		}
	}

}
</script>

<style>
	ul {
		list-style-type: none;
		padding-left: 0px;
		margin-top: 0;
	}
	li {
		display: flex;
		min-height: 50px;
		height: 50px;
		line-height: 50px;
		margin: 0.5rem 0;
		padding: 0 0.9rem;
		background: white;
		border-radius: 5px;
	}
	.checkBtn {
		line-height: 45px;
		color: #62acde;
		margin-right: 5px;
	}
	.removeBtn {
		margin-left: auto;
		color: #de4343;
	}

	/* 뷰 애니메이션 */
	/* https://vuejs.org/v2/guide/transitions */
	.list-item {
		display: inline-block;
		margin-right: 10px;
	}
	.list-move {
		transition: transform 1s;
	}
	.list-enter-active, .list-leave-active {
		transition: all 1s;
	}
	.list-enter, .list-leave-to {
		opacity: 0;
		transform: translateY(30px);
	}
</style>


