<template>
	<div class="clearAllContainer">
		<span class="clearAllBtn" type="button" @click="clearItem">Clear All</span>
	</div>
</template>

<script>
export default {
	methods: {
		clearItem(){
			this.$emit("removeAll")
		}
	}
}
</script>

<style scoped>
	.clearAllContainer {
		width: 8.5rem;
		height: 50px;
		line-height: 50px;
		background-color: white;
		border-radius: 5px;
		margin: 0 auto;
	}
	.clearAllBtn {
		color: #e20303;
		display: black;
	}
</style>


