<template>
  <div>
    <div class="inputBox shadow">
      <input type="text" @keyup.enter="addItem" v-model="newItem" placeholder="dahami">
      <span class="addContainer" v-on:click="addItem">
        <i class="addBtn fa fa-plus" aria-hidden="true"></i>
      </span>
      <modal :show="showModal" v-if="showModal" @close="closeModal">
        <h3 slot="header">경고</h3>
        <span slot="body">내용을 입력하세요.</span>
        <span slot="footer" @click="closeModal">
          <i class="closeModalBtn fa fa-times" aria-hidden="true"></i>
        </span>
      </modal>
    </div>
  </div>
</template>

<script>
import Modal from './common/Modal.vue';

export default {
  data() {
    return {
      newItem: '',
      showModal: false
    };
  },
  methods: {
    addItem() {
      // 예외처리 코드
      if (this.newItem !== '') {
        var item = this.newItem.trim();
        this.$emit('addItem', item);
        this.clearInput();
        // 입력창이 빈 칸 -> + 버튼 클릭했을 때 모달 출현.
      } else {
        this.showModal = true;
      }
    },
    clearInput() {
      this.newItem = '';
    },
    closeModal() {
      this.showModal = false;
    },
  },
  components: {
    Modal: Modal,
  },
};
</script>


<style scoped>
	.inputBox {
		background: white;
		height: 50px;
		line-height: 50px;
		border-radius: 5px;
	}
	input:focus {
		outline:none;
	}
	.inputBox input {
		border-style: none;
		font-size: 0.9rem;
	}
	.addContainer {
		float: right;
		background: linear-gradient(to right, #6478fb, #8763fb);
		display: inline-block;
		width: 3rem;
		border-radius: 0 5px 5px 0;
	}
	.addBtn {
		color: white;
		vertical-align: middle;
	}

</style>


